.header {
  max-width: 90rem;
  height: 60rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 6rem;
  font-family: "Roboto", sans-serif;
}
.header .container {
  display: flex;
  flex-direction: row;
  width: 61.25rem;
  height: 20.625rem;
  align-items: center;
  justify-content: space-between;
}
.header .container .image {
  width: 31.5625rem;
  height: 20.625rem;
}
.header .container .content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 23.75rem;
  height: 15.625rem;
}
.header .container .content__title {
  font-weight: 700;
  font-size: px-to-rem(42);
}
.header .container .content__text {
  font-weight: 300;
  line-height: 1.5rem;
}
.header .container .content__btn {
  width: 128px;
  padding: 16px 40px;
  border-radius: 32px;
  background: #E4C1AE;
  color: azure;
  border: none;
  cursor: pointer;
}
.header .container .content__btn:hover {
  background-color: #f0d0bf;
}
.header .container .content__btn:active {
  background-color: #dfb39c;
}